body {
    background-color: black;
}
  
.navbar {
    background-color: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 20%;
    color: white;
    font-family: Ubuntu;
    display: flex;
    justify-content: space-between;
    animation: fadeInAnimation ease 3s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
    z-index: 1;
}
  
.title {
    position: relative;
    margin-left: 2%;
}

.navbar > h1 {
    font-size: 3em;
    position: relative;
    top: 10%;
}
  
.navLink {
    display: inline-block;
    padding: 0 1vw;
    position: relative;
    right: 6%;
    top: 0;
    height: 100%;
    transition: 0.5s ease;
    border-bottom-left-radius: 20%;
    border-bottom-right-radius: 20%;
}

.navLink a {
    position: relative;
    top: 35%;
    text-decoration: none;
    color: white;
    font-size: 25px;
    transition: 0.5s ease;
}

.navLink:hover {
    background-color: rgba(0, 0, 255, 0.5);
    transition: 0.5s ease;
    box-shadow: 0 0 30px 20px rgba(0, 0, 255, 0.5);
    border-bottom-left-radius: 20%;
    border-bottom-right-radius: 20%;
    margin: 0 30px;
}

.navLink:hover > a {
    font-weight: bolder;
    color: rgb(34, 248, 255);
    transition: 0.7s ease;
}
  
.profile {
    position: relative;
    top: 30%;
    margin-top: 0.5%;
    margin-right: 5%;
}
  
.profile a {
    text-decoration: none;
    font-size: 25px;
    color: white;
    padding: 45% 15%;
    margin-right: 15%;
    background-color: rgb(0, 7, 48);
    border: 1px inset blue;
    border-radius: 5%;
    transition: 0.7s ease;
}

.profile a:hover {
    border-radius: 10%;
    background-color: rgb(5, 5, 139);
    border: 1px inset rgb(83, 83, 255);
    transition: 0.7s ease;
}
  
@keyframes fadeInAnimation {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
}